<template>
    <div class="star" :class="starType">
        <span class="star-item" :class="item" v-for="(item,index) in starClasses" :key="index"></span>
    </div>
</template>

<script>

    const CLS_ON = 'on'     //高亮
    const CLS_HALF = 'half' //半颗星星
    const CLS_OFF = 'off'   //不亮的星星
    const LENGTH = 5;       //默认显示5颗星星
export default {
    
    //接收父组件传递的值
    props:{
        score:Number,
        size:Number
    },
    computed:{

        starType(){
            return 'star-'+this.size;
        },
        //计算星星数量
        starClasses(){
            let score = Math.floor(this.score * 2) / 2;

            //全亮的星星
            let integer = Math.floor(score);

            //是否有小数
           let hasDecimal = score % 1 !== 0;
            //不亮的星星
           let result = [];
           for(let i = 0; i < integer; i++){
               result.push(CLS_ON)
           }
           if(hasDecimal){
               result.push(CLS_HALF)
           }
           while(result.length < LENGTH){
               result.push(CLS_OFF)
           }
           return result; 
        }
    }
    

}
</script>

<style lang="less" scoped>
    .star{
        font-size: 0;
        .star-item{
            display: inline-block;
            background-repeat: no-repeat;
        }
        &.star-24{
            .star-item{
                width: 10px;
                height: 10px;
                margin-right: 3px;
                background-size: 10px 10px;
                &:last-child{
                    margin-right: 0;
                }
                &.on{
                    background-image:url('star24_on@2x.png')
                }
                &.half{
                    background-image:url('star24_half@2x.png')
                }
                &.off{
                    background-image:url('star24_off@2x.png')
                }
            }
        }
        &.star-36{
            .star-item{
                width: 10px;
                height: 10px;
                margin-right: 3px;
                background-size: 10px 10px;
                &:last-child{
                    margin-right: 0;
                }
                &.on{
                    background-image:url('star24_on@2x.png')
                }
                &.half{
                    background-image:url('star24_half@2x.png')
                }
                &.off{
                    background-image:url('star24_off@2x.png')
                }
            }
        }
        &.star-48{
            .star-item{
                width: 10px;
                height: 10px;
                margin-right: 3px;
                background-size: 10px 10px;
                &:last-child{
                    margin-right: 0;
                }
                &.on{
                    background-image:url('star24_on@2x.png')
                }
                &.half{
                    background-image:url('star24_half@2x.png')
                }
                &.off{
                    background-image:url('star24_off@2x.png')
                }
            }
        }
    }
        
</style>




